html{height:100%}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.FF{margin:0;padding-bottom:25px;font-family:"Josefin Sans", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#fff;font-size:1.5em;-webkit-text-stroke:thin #000;paint-order:stroke fill;background:url(/Characters/static/media/party.ad5be195.png) no-repeat center center fixed;background-size:cover;overflow:hidden}.FF .quote{font-size:1.2em;font-style:italic}.FF h1{margin:0;text-align:center;font-family:"Permanent Marker", cursive;-webkit-text-stroke:2px black}.FF p{text-align:justify}.FF .paragraph{background-color:rgba(0,0,0,0.5);padding:2% 4%}.FF .galery{padding:0 50px}.FF .meet-the-sisters{padding:0 50px}.FF .title{font-family:"Permanent Marker", cursive;-webkit-text-stroke:2px black}.FF .card{display:flex;justify-content:space-between}.FF .card .left-side{display:flex;flex-direction:column;justify-content:center;padding-right:5%}.FF .card .right-side{justify-content:center;align-items:center;display:flex}.FF .card .right-side img{border:0.25em solid #5c0d0b;border-radius:15%;height:15em}.FF .switcher{display:flex;flex-wrap:wrap;justify-content:center}.FF .switcher .switcher-button{background-color:#5c0d0b;border-radius:10%;color:white;padding:16px;font-size:16px;border:none;margin:1%}.FF .switcher .switcher-button:hover{background-color:#f26d7d}@media screen and (max-width: 1200px){.FF .card{flex-direction:column-reverse}}@media screen and (max-width: 600px){.FF .slick-slider .right-side img{width:150px;height:100%}.FF .slick-slider .left-side{font-size:medium;font-size:initial}}

.home{display:flex;justify-content:center}.home h1{font-size:5em;font-weight:bold;animation:color-change 5s infinite;-webkit-text-stroke:2px #000;paint-order:stroke fill}@keyframes color-change{0%{color:red}15%{color:orange}30%{color:yellow}45%{color:green}60%{color:cyan}75%{color:blue}90%{color:purple}100%{color:red}}

html{height:100vh}body{min-height:100vh}#root{min-height:100vh}nav{background-color:#3ba9d4;padding:1% 0%}nav ul{margin:0;display:flex;list-style:none;justify-content:center}nav ul li{margin:0% 1%}nav ul li a{color:#fff;text-decoration:none}.button{color:#000000 !important;text-transform:uppercase;font-weight:bold;background:#ffffff;padding:5px 10px;border-radius:6px;display:inline-block;transition:all 0.5s ease 0s}.button:hover{color:#ffffff !important;background:#000000;border-radius:50px;transition:all 0.5s ease 0s}.button:disabled{color:#000000 !important;background:#7c7c7c}

.Quiz{margin:0;min-height:100vh;font-family:"Josefin Sans", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#fff;font-size:1.5em;-webkit-text-stroke:thin #000;paint-order:stroke fill;background:url(/Characters/static/media/party.ad5be195.png) no-repeat center center fixed;background-size:cover;overflow:hidden}.Quiz .quiz-wrap{display:flex;min-height:100vh;align-items:center;justify-content:center}.Quiz .quiz-wrap .quiz-form{display:flex;min-width:90%;min-height:90vh;background-color:#000000cb;align-items:center;justify-content:center}.Quiz .quiz-wrap .quiz-form .animatedArea{transition:all 1s ease-in-out}.Quiz .quiz-wrap .quiz-form form{-webkit-text-stroke:initial;paint-order:initial;display:flex;flex-direction:column;text-align:center;transition:all 1s ease-in-out}.Quiz .quiz-wrap .quiz-form form label{font-size:1.5em;margin-bottom:5%}.Quiz .quiz-wrap .quiz-form form input{font-size:1em;margin-bottom:5%;border-radius:15px;text-align:center}.Quiz .quiz-wrap .quiz-form form button{font-size:1em;margin-bottom:5%}

